@import "./utils";
.form_group {
	position: relative;
	margin-bottom: 18px;
}
.form_control {
	width: 493px;
	height: 70px;
	background: #FFFFFF;
	box-shadow: 0 0 30px rgba(0, 0, 0, 0.07);
	border-radius: 25px;
	border: none;
	padding: 0 30px;
	font-family: helveticaRegular, sans-serif;
	font-size: 25px;
	line-height: 70px;
	color: rgba(50,77,161,0.5);

	&:active,
	&:focus {
		box-shadow: 0 0 30px rgba(0, 0, 0, 0.07);
	}
	&:focus,
	&::placeholder {
		color: rgba(50,77,161,0.5);
	}
}

.calculator {
	position: relative;
	.btn {
		margin-top: 35px;
	}
}


.form__error {
	display: none;
	position: absolute;
	color: #feaa08;
	top: -3px;
	font-size: 12px;
	left: 0;
}
.form__error.is_error {
	display: block;
}
.section.user-data-form {
	min-height: 100vh;
	padding: 50px 0 30px;
	position: relative;
	overflow: hidden;

	.form_group {
		margin-bottom: 0;
		label:not(.drop_label) {
			position: absolute;
			z-index: 0;
		}
	}

	.container {
		max-width: 1070px;
		padding: 0 30px;
	}

	.title {
		margin-bottom: 45px;
		font-size: 35px;
		line-height: 1;
	}

	.btn {
		margin: 0 auto;
		font-family: helveticaBlack, sans-serif;
		font-size: 25px;
		line-height: 18px;
	}

	.drop_container .form__error,
	.form__error {
		left: 0;
		top: -24px;
	}

	.logo {
		position: absolute;
		top: 0;
		left: 100px;
	}

	.rect_74 {
		z-index: 1;
		width: 400px;
		height: 146px;
		bottom: -2px;
	}
	.rect_75 {
		z-index: 1;
		width: 1000px;
		height: 900px;
		bottom: -421px;
		right: -396px;
	}
	.rect_76 {
		z-index: 1;
		width: 560px;
		height: 900px;
		bottom: -580px;
		right: -10px;
	}
}
.drop_container {
	width: 100%;
	position: relative;
	z-index: 1;

	.form__error {
		left: 20px;
	}
}

.drop_label {
	width: 100%;
	height: 142px;
	background: #fff;
	border-radius: 25px;
	border: none;
	padding: 0 30px;
	box-shadow: 0 0 30px rgba(0, 0, 0, 0.07);
	font-family: helveticaRegular, sans-serif;
	font-size: 25px;
	line-height: 1;
	color: rgba(50,77,161,0.5);
	margin-bottom: 35px;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;

	&.drop_list {
		display: block;
		height: auto;
		padding: 30px 50px;

		.drop-placeholder {
			display: none;
		}
		.drop_img {
			width: 23px;
			height: 23px;
			margin: 0;
			border: none;
		}

		.drop_remove {
			border: none;
			background: transparent;
			transition: color 0.3s ease-in-out;

			&:hover {
				color: rgba(50,77,161,0.8);
			}
		}

		.drop_file {
			width: 100%;
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 15px;

			span {
				font-size: 20px;
				line-height: 20px;
			}
		}
	}
}

.drop_placeholder {
	font-family: helveticaRegular, sans-serif;
	font-size: 25px;
	line-height: 1;
	color: rgba(50,77,161,0.5);
}

.drop_input {
	position: absolute;
	top: 0;
	left: 0;
	width: 0;
	height: 0;
	overflow: hidden;
	opacity: 0;
}

.form_wrapper {
	display: grid;
	grid-gap: 30px;
	grid-template-areas:
			"email phone"
			"firstName lastName"
			"company ."
			"street city"
			"state code"
			"years ."
			"file file";

	@media (max-width: 1024px) {
		display: block;
		.drop_container {
			margin-top: 30px;
		}
		.form_group {
			margin-bottom: 30px !important;
		}
	}
}

.form_group {
	&_email {
		grid-area: email;
	}
			&_phone {
				grid-area: phone;
			}

	&_firstName {
		grid-area: firstName;
	}
	&_lastName {
		grid-area: lastName;
	}

	&_company {
		grid-area: company;
	}
	&_street {
		grid-area: street;
	}

	&_city {
		grid-area: city;
	}
	&_state {
		grid-area: state;
	}

	&_years {
		grid-area: years;
	}
	&_file {
		grid-area: file;
	}
}

.drop_img {
	width: 82px;
	height: 82px;
	margin-left: 30px;
	border: 2.3px dashed rgba(50,77,171,0.4);
	box-sizing: border-box;
	border-radius: 10px;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	z-index: 1;
}

.saturn_container {
	width: 550px;
	height: 550px;
	overflow: hidden;
	background: #59BD69;
	box-shadow: inset 58px 21px 66px rgba(0, 0, 0, 0.25);
	border-radius: 1000px;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 25px;
	z-index: 1;

	@media (max-width: 1500px) {
		width: 400px;
		height: 400px;
	}

	@media (max-width: 1024px) {
		position: initial;
		width: 300px;
		height: 300px;
		margin: 200px auto 50px;
	}
}
.saturn {
	position: absolute;
	width: 207px;
	height: 207px;
	border-radius: 50%;
	z-index: -1;

	-webkit-animation: myOrbit 4s linear infinite;
	-moz-animation: myOrbit 4s linear infinite;
	-o-animation: myOrbit 4s linear infinite;
	animation: myOrbit 4s linear infinite;

	@media (max-width: 1500px) {
		width: 150px;
		height: 150px
	}
	@media (max-width: 1024px) {
		width: 120px;
		height: 120px
	}


	svg {
		width: 100%;
		height: 207px;

		@media (max-width: 1500px) {
			height: 150px;
		}

		@media (max-width: 1024px) {
			height: 120px
		}
	}
}

