@-webkit-keyframes myOrbit {
	from { -webkit-transform: rotate(0deg) translateX(50px) rotate(0deg); }
	to   { -webkit-transform: rotate(360deg) translateX(50px) rotate(-360deg); }
}

@-moz-keyframes myOrbit {
	from { -moz-transform: rotate(0deg) translateX(50px) rotate(0deg); }
	to   { -moz-transform: rotate(360deg) translateX(50px) rotate(-360deg); }
}

@-o-keyframes myOrbit {
	from { -o-transform: rotate(0deg) translateX(50px) rotate(0deg); }
	to   { -o-transform: rotate(360deg) translateX(50px) rotate(-360deg); }
}

@keyframes myOrbit {
	from { transform: rotate(0deg) translateX(50px) rotate(0deg); }
	to   { transform: rotate(360deg) translateX(50px) rotate(-360deg); }
}
.ready .ready_container {
	padding: 0 25px!important;
	margin: 0!important;
	position: relative;

	.ready_info {
		max-width: 880px;
		@media (max-width: 1500px) {
			max-width: 580px;
		}
		@media (max-width: 1024px) {
			text-align: center;
			max-width: 100%;
			margin-top: 30px;

			.container {
				padding: 0;
			}
		}
	}
}
.ready {
	&.section.user-data-form  {
		padding: 0;
		display: flex;
		align-items: center;
		justify-content: center;

		@media (max-width: 1500px) {
			padding-top: 100px;
		}

		.container {
			max-width: 1570px;

			@media (max-width: 1500px) {
				max-width: 1024px;
			}

			@media (max-width: 1024px) {
				width: 100%;

				.col {
					width: 100%;
					flex-basis: 100%;
					max-width: 100%;
					text-align: center;
				}

				.row {
					display:  block;
				}
			}
		}

		.logo {
			top: 50px;
		}
	}
	.col:not(.col-5) {
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}
}
.ready_title {
	font-weight: 900;
	font-size: 81px !important;
	line-height: 75px;
	font-family: helveticaBlack, sans-serif;
	color: #4975FB;
	margin-bottom: 30px;

	@media (max-width: 1500px) {
		font-size: 52px !important;
		line-height: 50px;
		margin-bottom: 24px;
	}

	@media (max-width: 1024px) {
		font-size: 38px !important;
		text-align: center;
		margin-bottom: 20px !important;
	}
}

.ready_text {
	font-family: helveticaMedium, sans-serif;
	font-size: 40px;
	line-height: 150%;
	color: rgba(73,117,251,0.5);
	max-width: 880px;

	@media (max-width: 1500px) {
		font-size: 28px;
		line-height: 1.3;
		max-width: 90%;
	}

	@media (max-width: 1024px) {
		font-size: 20px !important;
		line-height: 1.3;
		text-align: center;
		margin: 0 auto 40px;
	}
}

@media (max-width: 1360px) {
	.form_control {
		width: 100%;
	}
}

@media (max-width: 1024px) {

	.ready_info {
		text-align: center;
		margin-top: 30px;

		.container {
			padding: 0;
		}
	}
	.drop_container .form__error,
	.form__error {
		top: -3px;
	}

	.drop_container {
		margin-top: 20px;
	}

	.section.user-data-form {
		padding-top: 80px;
		padding-bottom: 250px;
		position: relative;

		.logo {
			top: 20px;
			right: initial;
			left: 20px;

			@media (max-width: 1024px) {
				top: 30px;
				left: 50%;
				transform: translateX(-50%);

				svg {
					height: 30px;
				}
			}
		}

		.form_control {
			height: 80px;
			line-height: 80px;
			font-size: 25px;
			width: 100%;

			&::placeholder {
				font-size: 25px;
			}
		}
		.form_group {
			margin-bottom: 30px;
		}
		.col--right,
		.col--left {
			flex-basis: 50%;
		}

		.rect.rect_bottom_right {
			z-index: 0;
		}
		.rect.rect_bottom_right:nth-child(1) {
			width: 90px;
			bottom: -90px;
	}
		.rect.rect_bottom_right:nth-child(2) {
			width: 200px;
		}
		.rect.rect_bottom_right:nth-child(3) {
			width: 144px;
		}
	}

	@media (max-width: 768px) {
		.section.user-data-form {
			.col--right,
			.col--left {
				flex-basis: 100%;
			}
		}
	}
}