@import "./utils";
.hero_blog_info {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}
.hero_blog {
	background-image: url('../images/blog_hero.jpg');
}
.sidebar_banner_title {
	font-size: 35px;
	line-height: 135%;
	font-family: helveticaBlack, sans-serif;
}
.sidebar_banner_text {
	font-family: helveticaRegular, sans-serif;
	font-size: 18px;
	line-height: 135%;
	margin-bottom: 10px;
}
.sidebar_banner {
	border-radius: 45px;
	background-image: url('../images/covid.jpg');
	height: 350px;
	position: relative;
	overflow: hidden;
	color: #fff;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	margin-bottom: 70px;

	.sidebar_banner_info {
		position: relative;
		z-index: 1;
	}

	.btn--small {
		font-size: 16px;
		line-height: 16px;
		padding: 15px 32px;
	}
	
	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 0;
		background: rgba(50, 77, 161, 0.75);
	}
} 
.hero_blog_title {
	font-size: 60px;
	line-height: 120%;
	margin-bottom: 10px;
	font-family: helveticaBlack, sans-serif;
}
.hero_blog_text {
	font-family: helveticaRegular, sans-serif;
	font-weight: normal;
	font-size: 30px;
	margin: 0 auto;
	white-space: nowrap;
	line-height: 120%;
	text-align: center;
	color: #FFFFFF;
}
.more_btn {
	display: none;
}
.layout {
	display: grid;
	grid-template-columns: 1fr 350px;
	max-width: 1600px;
	padding: 98px 20px 150px;
	margin: 0 auto;
	grid-column-gap: 80px;
}
.col_sidebar {
	.blogs_card {
		border-radius: 45px;
		background: #FFFFFF;
		box-shadow: 0 0 30px rgba(0, 0, 0, 0.07);
		overflow: hidden;
		border: none;
		margin-bottom: 70px;


		.card_img-wrapper {
			width: 100%;
			height: 329px;
			border-top-left-radius: 25px;
			border-top-right-radius: 25px;
			position: relative;
			cursor: pointer;

			img {
				width: 100%;
				height: 329px;
				object-fit: cover;
				position: relative;
				z-index: 0;
			}

			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				background-color: rgba(73, 117, 251, 0.25);
				z-index: 1;
				transition: background-color 0.8s ease-in-out;
				cursor: pointer;
			}
		}

		.card:hover {
			.card_img-wrapper:after {
				background-color: transparent;
			}
		}

		.card_body {
			padding: 25px 32px 20px
		}

		.card_title {
			font-size: 25px;
			line-height: 25px;
			color: #4975FB;
			font-family: helveticaBold, sans-serif;
			margin-bottom: 15px;
			cursor: pointer;
			display: block;
			transition: color 0.3s ease-in-out;

			&:hover {
				color: rgba(73, 117, 251, 0.75);
			}
		}

		.card_text {
			font-size: 20px;
			line-height: 130%;
			color: rgba(50, 77, 161, 0.5);
			font-family: helveticaMedium, sans-serif;
			margin-bottom: 48px;
			height: 160px;
			overflow: hidden;
		}

		@media (max-width: 1500px) {
			.card_img-wrapper {
				img {
					height: 235px;
				}
			}

			.card_title {
				font-size: 19px;
				line-height: 19px;
			}

			.card {
				border-radius: 33px;
			}

			.card_body {
				padding: 19px 26px 6px;
			}


			.card_text {
				font-size: 14px;
				line-height: 130%;
				height: 56px;
				margin-bottom: 10px;
			}
		}
		@media (max-width: 1024px) {
			.blogs_card {
				margin: 12px;
				width: 260px !important;

			}


			.card_img-wrapper img{
				height: 191px;
			}

			.card_body {
				padding: 13px 20px;
			}
			.card_title {
				font-size: 15px;
				line-height: 1;
			}
			.card_text {
				max-height: 48px;
				font-size: 12px;
				line-height: 1.3;
				margin-bottom: 0;
			}
		}
	}

	.sidebar_title {
		font-family: helveticaMedium, sans-serif;
		font-size: 35px;
		line-height: 120%;
		margin-top: 48px;
		margin-bottom: 50px;
		color: #4975FB;
	}

}
.col_blog_content {
	.col_sidebar {
		padding-top: 70px;

		& > div:first-of-type {
			margin-bottom: 50px;
		}
	}

	p {
		color: rgba(50, 77, 161, 0.5);
		font-size: 25px;
		line-height: 150%;
		font-family: helveticaMedium, sans-serif;
		margin-bottom: 42px;
	}

	img {
		width: auto;
		height: auto;
		object-fit: cover;
		max-height: 450px;
		max-width: 100%;
		margin-bottom: 50px;
	}

	ul, ol {
		padding: 0;
		margin: 0;
		list-style-type: decimal;

		ul {
			list-style-type: lower-alpha;
		}
	}

	li {
		color: rgba(50, 77, 161, 0.5);
		font-size: 25px;
		line-height: 150%;
		font-family: helveticaMedium, sans-serif;
		margin-bottom: 30px;
	}

	a {
		color: rgba(50, 77, 161, 0.5);
		text-decoration: none;
		font-size: initial;
		line-height: inherit;
		transition: color 0.6s ease-in-out;

		&:hover {
			color: rgba(50, 77, 161, 0.7);
		}
	}
	h1,h2,h3,h4,h5,h6 {
		font-family: helveticaBold, sans-serif;
		font-style: normal;
		font-weight: bold;
		font-size: 50px;
		color: #4975FB;
		text-align: left;
	}
	h1 {
		font-size: 70px;
		text-align: center;
		margin-bottom: 20px;
	}
	h2 {
		font-family: helveticaRegular, sans-serif;
		font-size: 25px;
		line-height: 120%;
		color: rgba(50, 77, 161, 0.5);
		margin-bottom: 52px;

		span {
			display: block;
			margin-top: 32px;
		}
	}
	h3 {
		font-size: 50px;
		line-height: 75px;
		margin-bottom: 10px;
	}
	h4 {
		font-size: 25px;
		line-height: 150%;
	}
	h5 {
		font-size: 20px;
		margin-bottom: 10px;
	}
	h6 {
		font-size: 18px;
		margin-bottom: 8px;
	}

	.network_wrap {
		width: 350px;
		background: rgba(73, 117, 251, 0.05);
		border-radius: 25px;
		padding: 30px 35px;
		.network_item {
			margin-bottom: 0;
		}

		.network {
			justify-content: flex-start;
			width: 100%;

			a {
				display: block;
				width: 21px;
				height: 21px;
				margin-right: 30px;
				margin-bottom: 0;

				&:hover {
					svg, path {
						cursor: pointer;
						fill: rgba(73, 117, 251, 0.75)
					}
				}
			}
		}
		svg {
			width: 100%;
		}
		svg, path {
			fill: #4975FB
		}
	}

}

@media(max-width: 1500px) {
	.hero_blog {
		height: 525px;
	}
	.layout {
		max-width: 1140px;
		grid-template-columns: calc(100% - 255px) 260px;
		grid-column-gap: 65px;
		padding: 70px 20px 115px;
	}
	.hero_blog_title {
		font-size: 44px;
		line-height: 120%;
		text-align: center;
	}
	.hero_blog_text {
		font-size: 23px;
		line-height: 120%;
		text-align: center;
	}

	.col_blog_content {
		h2 {
			font-size: 19px;
			line-height: 120%;
			color: rgba(50, 77, 161, 0.85);
			margin-bottom: 40px;

			span,
			.blog_date {
				margin-top: 19px;
			}
		}

		p, li, a {
			font-size: 18.2px;
			line-height: 150%;
		}
		p:not(:last-of-type) {
			margin-bottom: 30px;
		}

		img {
			height: 340px;
			margin-bottom: 32px;
			margin-top: 15px;
		}

		h3 {
			font-size: 37.5px;
			line-height: 150%;
		}

		.network_wrap {
			padding: 22px 30px;
			width: 265px;

			h4 {
				font-size: 18.75px;
				line-height: 150%;
			}

			.network a {
				margin-right: 14px;
				margin-bottom: 0;

				svg {
					width: 17px;
					height: 17px;
				}
			}
		}
	}

	.col_sidebar {
		.sidebar_title {
			font-size: 26.25px;
			line-height: 120%;
			margin-top: 35px;
			margin-bottom: 38px;
		}
		.sidebar_banner {
			height: 262px;
			margin-bottom: 72px !important;
			.btn {
				padding: 6px 20px;
				font-size: 12px;
				line-height: 12px;
				width: 116px;
				height: 34px;
			}
		}
		.sidebar_banner,
		.blogs_card {
			margin-bottom: 54px;
			border-radius: 34px;

			.card_body {
				padding: 19px 26px 19px;
			}

			.card_text {
				font-size: 14px;
				line-height: 136%;
				height: 111px;
				margin-bottom: 42px;
			}

			.card_img-wrapper {
				height: 248px;
				img {
					height: 248px;
				}
			}
		}
	}
	.sidebar_title {
		font-size: 26.25px;
		line-height: 120%;
	}
	.sidebar_banner_text {
		font-size: 13.5px;
		line-height: 135%;
		max-width: 176px;
		margin: 0 auto 10px;
	}
	.sidebar_banner_title {
		font-size: 26.25px;
		line-height: 135%;
		margin-top: 10px;
	}
}
@media (max-width: 1360px) {
	.layout {
		max-width: 100%;
		grid-template-columns: calc(100% - 355px) 260px;
	}
	.hero_blog_title {
		width: 80%;
		margin: auto;
	}
}
.more_btn {
	display: none;
}
@media(max-width: 1024px) {
	.layout {
		display: block;
		width: 100%;
	}

	.collapsed_text {
		height: 264px;
		overflow: hidden;
		padding-bottom: 30px;
		position: relative;
		
		&.show {
			height: auto;
			overflow: visible;

			.p:last-child {
					margin-bottom: 0;
			}

			.preview_text {
				height: auto;
				overflow: visible;
			}

			.more_btn {
				display: none;
			}
		}
	}
	.preview_text {
		height: 194px;
		overflow: hidden;
	}

	.more_btn {
		background: transparent;
		border: none;
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		text-align: center;
		color: rgba(73, 117, 251, 0.75);
		font-family: helveticaMedium, sans-serif;
		font-size: 15px;
		line-height: 150%;
		transition: color 0.3s ease-in-out;
		z-index: 1;
		display: block;
		span {
			position: relative;
			z-index: 0;
			display: block;
		}

		&:hover {
			color: rgba(73, 117, 251, 0.8);
		}

		svg, path {
			fill: rgba(73, 117, 251, 0.75);
			position: relative;
			z-index: 0;
		}
	}

	.hero_blog_title {
		width: initial;
	}

	.hero_blog {
		height: 812px;
	}
	.layout {
		max-width: 1140px;
		grid-template-columns: calc(100% - 255px) 260px;
		grid-column-gap: 65px;
		padding: 70px 20px 115px;
	}
	.hero_blog_title {
		font-size: 40px;
		line-height: 120%;
	}
	.hero_blog_text {
		font-size: 15px;
		line-height: 120%;
	}

	.col_blog_content {
		h2 {
			font-size: 18px;
			line-height: 120%;
			margin-bottom: 38px;

			span,
			.blog_date {
				margin-top: 18px;
			}
		}

		p, li, a {
			font-size: 18px;
			line-height: 28px;
		}
		p {
			margin-bottom: 27px;
		}

		img {
			height: 154px;
			margin-bottom: 35px;
			margin-top: 43px;
		}

		h3 {
			font-size: 35px;
			line-height: 150%;
		}
		.blog_body li {
			margin-bottom: 40px;
			position: relative;
		}

		.network_wrap {
			width: 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;

			h4 {
				font-size: 18.75px;
				line-height: 150%;
				margin: 0 auto;
			}

			.network {
				justify-content: center;
			}
			.network a {
				margin-right: 14px;
				margin-bottom: 0;

				svg {
					width: 17px;
					height: 17px;
				}
			}
		}
	}

}
