@import "./utils";

.hero {
	background-image: url("../images/img_hero.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	background-attachment: fixed;
	background-position: center center;
	min-height: 900px;
	padding: 40px 0;
	color: $light;
	position: relative;
	overflow: hidden;

	display: flex;
	align-items: center;
	justify-content: flex-start;

	.rect_74 {
		z-index: 1;
		width: 400px;
		height: 146px;
		bottom: -2px;
	}
	.rect_75 {
		z-index: 1;
		width: 1000px;
		height: 900px;
		bottom: -421px;
		right: -396px;
	}
	.rect_76 {
		z-index: 1;
		width: 560px;
		height: 900px;
		bottom: -580px;
		right: -10px;
	}

	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		background: rgba(73,115,251,0.85);
		z-index: 0;
	}

	.container {
		position: relative;
		z-index: 1;
	}

	&_title {
		font-family: helveticaBold, sans-serif;
		font-size: 60px;
		line-height: 72px;
		margin-bottom: 5px;
	}

	&_subtitle {
		line-height: 40px;
		font-size: 30px;
		opacity: 0.8;
		margin-bottom: 25px;
	}

	&_text {
		max-width: 358px;
		font-size: 15px;
		line-height: 1.2;
		margin-bottom: 38px;
	}
}

.copyright {
	background: $dark;
	color: #fff;
	font-family: helveticaMedium, sans-serif;

	.row {
		justify-content: space-between;
		align-items: center;
	}
}

.why {
	background: url("../images/img_hiw.jpg") no-repeat center center fixed;
	background-size: cover;
	overflow: hidden;
	min-height: 685px;
	padding: 40px 0;
	color: $light;
	position: relative;
	text-align: center;

	.title {
		font-size: 55px;
		line-height: 1;
		font-family: helveticaBold, sans-serif;
	}

	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		//background: rgba(73, 115, 251, 0.85);;
		background: rgba(97, 136, 255, 0.85);
		//background: rgba(55,77,161,0.85);
		z-index: 0;
	}

	.container {
		position: relative;
		z-index: 1;
	}

	&_text {
		max-width: 1000px;
		margin: 25px auto 40px;
		font-family: helveticaMedium, sans-serif;
		font-size: 35px;
		line-height: 1.5;
		text-align: center;
	}
}
.section.section_calculator {
	padding: 0 0 126px;

	.col--left {
		padding-left: 186px;
		flex-basis: 40%;
	}

	.col--right {
		padding-left: 106px;
		flex-basis: 50%;
	}
	.title {
		margin-bottom: 64px;
	}
}

.section.section_reviews {
	padding-bottom: 70px;

	.title {
		margin-bottom: 15px;
	}
	.subtitle {
		margin-bottom: 60px;
	}

	.col--left {
		flex-basis: 50%;
	}
	.col--right {
		overflow: hidden;
		flex-basis: 25%;
	}
}


.testimonials_list {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 72px;
}
.testimonials_photo {
	border-radius: 50%;
	width: 150px;
	height: 150px;
	object-fit: cover;
	margin: 0 auto 25px;
}
.testimonials_title {
	font-size: 30px;
	line-height: 1;
	color: $main;
	margin-bottom: 20px;
}
.testimonials_card {
	width: 350px;
	height: 477px;
	background: $light;
	box-shadow: 0 0 30px rgba(0, 0, 0, 0.07);
	border-radius: 30px;
	padding: 35px 25px 60px;
	font-family: helveticaMedium, sans-serif;
	font-size: 20px;
	line-height: 1.3;
	text-align: center;
	color: rgba(39, 39, 39, 0.8);
	position: relative;
	z-index: 0;
	overflow: hidden;
}

.section_reviews {
	.approve_title {
		margin-top: 75px;
		font-size: 55px;
		line-height: 1.2;
	}

	.approve_text {
		font-family: helveticaRegular, sans-serif;
		font-size: 25px;
		line-height: 1;
		margin-bottom: 50px;
	}
}

.statistic {
	.title {
		margin-bottom: 74px;
	}
	.col--right {
		flex-basis: calc(100% - 313px);
		padding-left: 0;
		position: relative;
		z-index: 0;
	}
	.col--left {
		padding-left: 0;
		padding-right: 0;
		flex-basis: 313px;
		position: relative;
		z-index: 1;
	}
	&_item {
		position: relative;
		padding-bottom: 46px;
		padding-top: 39px;

		&:nth-child(1) {
			left: -200px;

			.statistic_title {
				left: 212px;
			}
		}
		&:nth-child(2) {
			left: -140px;

			.statistic_title {
				left: 152px;
			}
		}
		&:nth-child(3) {
			left: -50px;
		}

		.statistic_title {
			left: 62px;
		}
	}

	&_line {
		width: 600px;
		height: 30px;
		background: $main;
		border-radius: 18px;
		position: relative;
	}

	&_point {
		background: $light;
		box-shadow: 0 0 9px rgba(0, 0, 0, 0.2);
		width: 46px;
		height: 46px;
		border-radius: 50%;
		position: absolute;
		left: 87%;
		top: -10px;
	}

	&_img {
		background: $light;
		padding: 38px 44px;;
		border-radius: 25px;
		box-shadow: 0 0 24px rgba(0, 0, 0, 0.07);
	}

	&_title {
		position: absolute;
		top: 0;
		font-family: helveticaMedium, sans-serif;
		font-size: 25px;
		line-height: 1;
		color: $main;
	}

	&_number {
		position: absolute;
		left: 634px;
		top: 58px;
		font-family: helveticaBold, sans-serif;
		font-size: 32px;
		line-height: 1px;
		color: $accent2;
	}
}

[data-aos="my-animation"] {
	transform: translateX(-600px);
	transition-property: transform;
}
/* After animation */
[data-aos="my-animation"].aos-animate {
	transform: translateX(0);
}

[data-aos="rotate-right-bottom"] {
	transform: rotate(60deg) translateX(300px);
	opacity: 0;
	transition-property: transform;
}
/* After animation */
[data-aos="rotate-right-bottom"].aos-animate {
	transform: rotate(0) translateX(0);
	opacity: 1;
}
.industries_text,
.cash_text,
.testimonials_text {
	color: rgba(39, 39, 39, 0.8);
}
.industries .cash_title,
.cash .cash_title {
	margin-bottom: 40px;
	line-height: 1.2;
}
.cash .cash_text {
	color: rgba(50,77,161, 0.5);
}

.industries .cash_title {
	margin-bottom: 25px;
}
.industries_text,
.cash_text {
	padding-right: 120px;
	font-family: helveticaMedium, sans-serif;
	font-size: 25px;
	line-height: 1.5;
}
.industries_text {
	font-size: 20px;
	font-family: helveticaRegular, sans-serif;
	padding-right: 60px;
	margin-bottom: 40px;
}
.cash_subtitle {
	font-family: helveticaMedium, sans-serif;
	font-size: 25px;
	line-height: 25px;
	color: $main;
	margin-bottom: 15px;

}

.cash .col--right {
	margin-top: -50px;
}

.cash .cash_title_group {
	margin-bottom: 60px;
}