@import "./utils";

.slider_wrapper{
	max-width: 100%;
	position: relative;
	overflow: hidden;
}
.slider{
	width: 1500px;
	position: absolute;
	top: 0;
	left: 50%;
	transform: translateX(-50%);
}
.slider_card_3 {
	transform: translateY(60px)  translateX(0px);
	z-index: 1;
	.img_active,
	.img {
		opacity: 1;
		display: block;
	}
}
.slider_card_4{
	transform: translateY(30px)  translateX(-72px);
	z-index: 0;
	.img_active,
	.img {
		opacity: 0.7;
		display: block;
	}
}
.slider_card_5 {
	transform: translateY(0px) translateX(-144px);
	z-index: -1;
	.img_active,
	.img {
		opacity: 0.5;
		display: block;
	}
}
.slider_card_1 {
	transform: translateY(0px)  translateX(144px);
	z-index: -1;

	-webkit-animation: slide-br_1 0.5s cubic-bezier(0.680, -0.550, 0.265, 1.550) both;
	animation: slide-br 0.5s cubic-bezier(0.680, -0.550, 0.265, 1.550) both;
	.img_active,
	.img {
		opacity: 0.7;
	}
}
.slider_card_2 {
	transform: translateY(30px)  translateX(72px);
	z-index: 0;
	.img_active,
	.img {
		opacity: 0.5;
	}

	-webkit-animation: slide-br_2 0.5s cubic-bezier(0.680, -0.550, 0.265, 1.550) both;
	animation: slide-br 0.5s cubic-bezier(0.680, -0.550, 0.265, 1.550) both;
}
.slider_card_0 {
	position: absolute;
	padding: 0 !important;
	top: 0;
	left: 0;
	opacity: 0;
	width: 0;
	height: 0;
	overflow: hidden;
}

.slider_card {
	display: inline-block;
	flex-basis: 200px;
	width: 200px;
	height: 250px;
	background: $light;
	border-radius: 25px;
	padding: 40px 36px;
	color: $light;
	text-align: center;
	box-shadow: 0 0 22px rgba(0, 0, 0, 0.07);
	position: relative;

	.img {
		display: none;
		margin: auto;
	}
	.img_active {
		margin: auto;
		display: block;
	}

	&.slick-cloned {
		background: $light;
	}

	&.active_slide {
		background: $main;
		.img {
			opacity: 1;
			display: block;
		}
		.img_active {
			display: none;
		}
	}
}

.flip_list {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 1250px;
	margin: auto;
}
.flip_text {
	font-family: helveticaMedium, sans-serif;
	font-size: 23px;
	line-height: 130%;
	color: rgba(39, 39, 39, 0.8);
}
.flip_card {
	width: 257px;
	height: 290px;
	img {
		display: inline-block;
		margin-bottom: 22px;
	}

	.front {
		text-align: center;
		background: $light;
		border-radius: 30px;
		background: $light;
		padding: 52px 50px;
		color: $main;
		font-size: 25px;
		line-height: 25px;
		font-family: helveticaBold, sans-serif;
		box-shadow: 0 0 30px rgba(0, 0, 0, 0.07);
	}
	.back {
		text-align: center;
		background: $light;
		border-radius: 30px;
		background: $light;
		padding: 70px 23px;
		font-family: helveticaMedium, sans-serif;
		font-size: 23px;
		line-height: 1.33;
		color: rgba(39, 39, 39, 0.8);
		box-shadow: 0 0 30px rgba(0, 0, 0, 0.07);
	}

	&flip_card--second .front {
		padding: 37px 50px;
	}
	&.flip_card--third .front img {
		margin-bottom: 30px;
	}
	&.flip_card--first .back {
		padding: 80px 18px 50px 18px;
		line-height: 1.33;
	}

	&.flip_card--second .back {
		padding: 84px 14px 50px 14px;
		line-height: 1.33;
	}
	&.flip_card--third .back {
		padding: 84px 19px 50px 20px;
		line-height: 1.33;
	}
}

.flip_card--first .back,
.flip_card--first .front{
	background: url('../images/oval_top_right.svg')  left 0 top 0 no-repeat,
							url('../images/rectangle_90.svg') right 0 bottom 0 no-repeat;

	background-size: auto, 50px;
}

.flip_card--second .back,
.flip_card--second .front{
	background: url('../images/oval_top_left.svg')  right 0 top 0 no-repeat,
	url('../images/rectangle_85.svg') left 0 bottom 0 no-repeat,
	url('../images/rect_mini_bittom_right.svg') right 0 bottom 0 no-repeat;

	background-size: auto, 50px, auto;
}

.flip_card--third .back,
.flip_card--third .front{
	background: url('../images/oval_bottom_left.svg')  left 0 bottom 0 no-repeat,
	url('../images/rect_mini_bittom_right.svg') left 0 top 0 no-repeat;

	background-size: auto, 50px;
}
.flip_card--first,
.flip_card--second {
	&:after {
		content: '';
		position: absolute;
		width: 28px;
		height: 15px;
		background-image: url(../images/arrow_right.svg);
		background-position: center center;
		background-repeat: no-repeat;
		background-size: contain;
		top: 50%;
		left: 150%;
	}
}

.flip_title {
	color: #324DA1;
}

	@-webkit-keyframes slide-br_1 {
	0% {
		-webkit-transform:   translateY(0px)  translateX(144px);
		transform:   translateY(0px)  translateX(144px);
	}
	100% {
		transform: translateY(60px)  translateX(0px);
		-webkit-transform: translateY(60px)  translateX(0px);
	}
}

@keyframes slide-br_1 {
	0% {
		-webkit-transform:   translateY(0px)  translateX(144px);
		transform:   translateY(0px)  translateX(144px);
	}
	100% {
		transform: translateY(60px)  translateX(0px);
		-webkit-transform: translateY(60px)  translateX(0px);
	}
}

@-webkit-keyframes slide-br_2 {
	0% {
		-webkit-transform:  translateY(30px)  translateX(72px);
		transform:  translateY(30px)  translateX(72px);
	}
	100% {
		transform: translateY(60px)  translateX(0px);
		-webkit-transform: translateY(60px)  translateX(0px);
	}
}

@keyframes slide-br_2 {
	0% {
		-webkit-transform:  translateY(30px)  translateX(72px);
		transform:  translateY(30px)  translateX(72px);
	}
	100% {
		transform: translateY(60px)  translateX(0px);
		-webkit-transform: translateY(60px)  translateX(0px);
	}
}
