@import "./utils";

.footer {
	background: $main;
	padding: 40px 0;
	color: $light;

	.container {
		position: relative;
		z-index: 0;
	}

	&_title {
		font-size: 25px;
		font-family: helveticaBold, sans-serif;
		line-height: 1;
		margin-bottom: 20px;
	}

	&_link {
		font-size: 20px;
		line-height: 1;
		margin-bottom: 15px;
	}

	&_item {
		margin-bottom: 15px;
	}

	&_partner {
		margin-bottom: 15px;
	}

	&_logo {
		margin-bottom: 15px;
		display: block;
	}

	address {
		display: block;
		margin-bottom: 5px;
	}


	.col_1 {
		flex-basis: 150px;
	}
	.col_2 {
		flex-basis: 250px;
		padding-left: 70px;
	}
	.col_3 {
		flex-basis: 70px;
	}
	.col_4 {
		margin-left: 10px;
		flex-basis: 100px;
	}
	.col_5 {
		flex-basis: 120px;
		margin-left: 50px;
	}
	.col_6 {
		flex-basis: 120px;
	}
}
.network {
	display: flex;
	width: 110px;
	justify-content: space-between;
	align-items: center;
}
.copyright {
	background: $dark;
	color: #fff;
	font-family: helveticaMedium, sans-serif;

	.row {
		justify-content: space-between;
		align-items: center;
	}
}
