@import "./utils";
@import "./reset";
@import "./header";
@import "./content";
@import "./slider";
@import "./form";
@import "./footer";

@import "./blogs";
@import "./_blog";

@font-face {
	font-display: swap;
	font-family: "helveticaBlack";
	src: url("../fonts/HelveticaBlack.eot");
	src: url("../fonts/HelveticaBlack.eot") format("embedded-opentype"),
	url("../fonts/HelveticaBlack.woff") format("woff"),
	url("../fonts/HelveticaBlack.ttf") format("truetype")
}

@font-face {
	font-display: swap;
	font-family: "helveticaBold";
	src: url("../fonts/HelveticaBold.eot");
	src: url("../fonts/HelveticaBold.eot") format("embedded-opentype"),
	url("../fonts/HelveticaBold.woff") format("woff"),
	url("../fonts/HelveticaBold.ttf") format("truetype")
}

@font-face {
	font-display: swap;
	font-family: "helveticaMedium";
	src: url("../fonts/HelveticaMedium.eot");
	src: url("../fonts/HelveticaBold.eot") format("embedded-opentype"),
	url("../fonts/HelveticaBold.woff") format("woff"),
	url("../fonts/HelveticaBold.ttf") format("truetype")
}

@font-face {
	font-display: swap;
	font-family: "helveticaRegular";
	src: url("../fonts/HelveticaRegular.eot");
	src: url("../fonts/HelveticaRegular.eot") format("embedded-opentype"),
	url("../fonts/HelveticaRegular.woff") format("woff"),
	url("../fonts/HelveticaRegular.ttf") format("truetype")
}


html {
	overflow: auto;
	scroll-behavior: smooth;
}

.body {
	font-family: "helveticaRegular", sans-serif;
	font-size: 13px;
	line-height: 27px;
	color: $text-main;
	background: #fff;
}

.flex-container {
	@include flex(space-between, flex-start, wrap);
}

.section {
	position: relative;
	padding: 140px 0 150px;
	margin: 0;

	&--light {
		.title {
			color: $main;
		}
	}

	&--dark {
		.title {
			color: $light;
		}
	}
}

.container {
	width: 100%;
	max-width: 768px;
	padding: 0 25px;
	margin: 0 auto;

	@media (min-width: 1024px) {
		max-width: 1150px;
		padding: 0 20px;
	}

	@media (min-width: 1500px) {
		max-width: 1460px;
		padding: 0 15px;
	}

}

.logo {
	display: block;
	transform: scale(1.02);
	transition: transform 0.2s ease;

	&:hover {
		transform: scale(1.02);
	}

	@media (max-width: 1500px) {
		width: 131px;
		height: 35px;

		svg {
			height: 35px;
			width: 100%;
			object-fit: contain;

		}
	}
}
.btn {
	cursor: pointer;
	background: $accent;
	height: 65px;
	border: 4px solid $accent;
	padding: 18px 37px;
	border-radius: 30px;
	color: $light;
	font-family: helveticaBold, sans-serif;
	font-style: normal;
	font-size: 21px;
	line-height: 21px;
	box-shadow: 0 0 40px rgba(121, 255, 142, 0.15);
	transition: background-color 0.3s ease-in-out,
					color 0.3s ease-in-out;


	&:hover {
		background: $light;
		color: $accent2;
	}
}

.fixed {
	&_btn {
		background: #fff;
		transition: background-color 0.3s ease-in-out,
			box-shadow 0.3s ease-in-out;
		padding: 20px;
		width: 165px;
		height: 132px;
		position: fixed;
		left: 30px;
		bottom: 30px;
		z-index: 200;
		box-shadow: 1px 4px 13px rgba(0, 0, 0, 0.5);
		border-radius: 17px;
		text-align: center;

		.btn {
			font-family: helveticaBold, sans-serif;
			font-size: 14px;
			line-height: 1;
			padding: 12px 30px;
			height: auto;
			width: auto;

			&:hover {
				color: $light;
				background: $accent;
			}
		}

		&:hover {
			box-shadow: -1px 3px 8px rgba(0, 0, 0, 0.6);
			background-color: $main;
			.fixed_text {
				color: $light;
			}
		}
	}
	&_text {
		color: $main;
		font-family: helveticaBold, sans-serif;
		transition: color 0.3s ease-in-out;
		line-height: 1.35;
		font-size: 14px;
		margin-bottom: 10px;
	}
}

.cash_title,
.title {
	color: $main;
	font-size: 50px;
	line-height: 1;
	margin-bottom: 20px;
	font-family: helveticaBold, sans-serif;
}

.subtitle {
	font-size: 25px;
	line-height: 25px;
	color: rgba( 50, 77, 161, 0.5);
}
.card_info {
	position: relative;
	z-index: 10;
}
.accent {
	color: $accent;
}

.rect {
	position: absolute;
	z-index: 1;
	background-position: center center;
	background-size: contain;
	background-repeat: no-repeat;
}

.rect_14 {
	background-image: url('../images/oval_top_left.svg');
	width: 90px;
	height: 90px;
}

.rect_15 {
	background-image: url('../images/rect_87.svg');
	width: 70px;
	height: 70px;
}
.rect_90 {
	background-image: url('../images/rect_90.svg');
	width: 136px;
	height: 136px;
}
.rect_85 {
	background: transparent;
	border-radius: 32px;
	border: 30px solid rgba(73, 117, 251,0.8);
	border-top-left-radius: 0;
	border-bottom-right-radius: 0;
	width: 0;
	height: 0;
}

.flip_card .rect_85 {
	border-radius: 24px;
	border: 24px solid rgba(73, 117, 251,0.8);
	border-top-left-radius: 0;
	border-bottom-right-radius: 0;
}
.rect_86 {
	background: transparent;
	border-radius: 32px;
	border: 30px solid rgba(73, 117, 251,0.8);
	border-bottom-left-radius: 0;
	border-top-right-radius: 0;
	width: 0;
	height: 0;
}
.rect_87 {
	background-image: url('../images/rect_87.svg');
	width: 136px;
	height: 136px;
}

.rect_74 {
	background-image: url('../images/Rectangle 74.svg');
	width: 136px;
	height: 136px;
}

.rect_75 {
	background-image: url('../images/Rectangle 75.svg');
	width: 136px;
	height: 136px;
}

.rect_76 {
	background-image: url('../images/Rectangle 76.svg');
	width: 136px;
	height: 136px;
}
.rect_top_left {
	top: 0;
	left: 0;
}
.rect_top_right {
	top: 0;
	right: 0;
}
.rect_bottom_right {
	bottom: 0;
	right: 0;
}
.rect_bottom_left {
	bottom: 0;
	left: 0;
}


.rect_104 {
	background-image: url('../images/tabs/rect_104.svg');
	width: 236px;
	height: 225px;
}

.rect_105 {
	background-image: url('../images/tabs/rect_105.svg');
	width: 525px;
	height: 201px;
}

.rect_106 {
	background-image: url('../images/tabs/rect_106.svg');
	width: 462px;
	height: 196px;
}

.arrow-down {
	width: 15px;
	height: 40px;
	background-image: url(../images/arrow_down.svg);
	background-position: center center;
	background-size: contain;
	background-repeat: no-repeat;
	position: absolute;
	bottom: 50px;
	left: 50%;
	z-index: 2;

	transform: translateX(-50%);

	-webkit-animation-duration: 1s;
	animation-duration: 1s;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
	-webkit-transform-origin: left center;
	-ms-transform-origin: left center;
	transform-origin: left center;
	-webkit-animation-name: swing;
	animation-iteration-count: infinite;
	animation-name: swing;
}


@-webkit-keyframes swing {
	0% {
		transform: translate(-50%, -2px) skewY(5deg);
	}
	50% {
		transform: translate(-50%, -4px) skewY(-8deg);
	}
	100% {
		transform: translate(-50%, 0px) skewY(5deg);
	}
}

@keyframes swing {
	0% {
		transform: translate(-50%, -2px) skewY(5deg);
	}
	50% {
		transform: translate(-50%, -4px) skewY(-8deg);
	}
	100% {
		transform: translate(-50%, 0px) skewY(5deg);
	}
}

@media (max-width: 1500px) {
	.btn {
		font-size: 16px;
		line-height: 16px;
		padding: 12px 20px;
		width: 146px;
		height: 48px;
		box-shadow: 0 0 30px rgba(121, 255, 142, 0.15);
		border-radius: 23px;
	}
}

@media (max-width: 1500px) {
	.section {
		padding: 98px 100px;
	}

	.fixed_text {
		font-size: 10px;
		line-height: 1.35;
		margin-bottom: 7px;
	}

	.fixed_btn {
		width: 124px;
		height: 99px;
		border-radius: 12px;
		padding: 12px;
	}
	.fixed_btn .btn {
		font-size: 12px;
		padding: 9px 22px;
		width: auto;
	}
}

.up_btn {
	background: rgba(105, 216, 122, 0.25);
	border: 2px solid #69D87A;
	box-sizing: border-box;
	border-radius: 8px;
	display: flex;
	width: 54px;
	height: 54px;
	cursor: pointer;
	position: absolute;
	right: -17px;
	top: 0;
	z-index: 10;

	&:hover {
		background: rgba(105, 216, 122, 0);
	}

	svg {
		display: block;
		margin: auto;
	}
}

.lg_visible {
	@media (min-width: 1601px) {
		display: block;
	}
	@media (max-width: 1500px) {
		display: none;
	}
}

.md_visible {
	display: none;

	@media (max-width: 1500px) {
		display: block;
	}
	@media (max-width: 1024px) {
		display: none;
	}
}

.sm_visible {
	display: none;

	@media (max-width: 1024px) {
		display: block;
	}
}
.no_padding {
	padding: 0 !important;
}
.no_space {
	white-space: nowrap;
}
@media (min-width: 1501px) {
	.lg_hidden {
		display: none;
	}
}

@media (max-width: 1024px) {
	.sm_hidden {
		display: none;
	}
	.sm_no_padding {
		padding: 0 !important;
	}
}

@media (min-width: 1025px) {
	.md_hidden {
		display: none;
	}
}

@media (min-width: 1501px) {
	.lg_hidden {
		display: none;
	}
}

img {
	opacity: 1;
	transition: opacity 0.3s;
}

img[data-src] {
	opacity: 0;
}