@import "./utils";

.light_char {
	fill: $main;
}

.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	background: rgba(255, 255, 255, 0.85);
	box-shadow: 1px 4px 13px rgba(0, 0, 0, 0.1);
	width: 100%;
	height: 120px;
	padding: 35px 0;
	z-index: 10;

	&--top {
		position: absolute;
		background: transparent;
		box-shadow: none;

		.light_char {
			fill: $light;
		}
	}

	&--dark {
		background: $main;
	}
}

.main_nav {
	.nav_list {
		top: 136px;
		right: calc((100vw - 1460px) / 2 - -15px);
		background: rgba(255, 255, 255, 0.9);
		transition: background-color 0.3s ease-in-out,
		box-shadow 0.3s ease-in-out;
		padding: 20px;
		width: 180px;
		position: fixed;
		z-index: 200;
		box-shadow: 1px 4px 13px rgba(0, 0, 0, 0.5);
		border-radius: 17px;
		text-align: right;

		-webkit-transform: translateX(150px) translateZ(130px) rotateY(-90deg);
		transform: translateX(150px) translateZ(130px) rotateY(-90deg);

		.nav_item:not(:last-of-type) {
			.nav-link {
				padding-bottom: 18px;
			}
		}

		@media (max-width: 1500px) {
			top: 110px;
			right: calc((100vw - 1150px) / 2 - -20px);
		}
	}

	.nav_item {
		border-right: 2px solid rgba(50,77,161,0.5);

		&.active {
			border-right: 2px solid rgba(50,77,161,1);

			.nav_link {
				color: rgba(50,77,161,1);
			}
		}
	}

	.nav_link {
		font-size: 18px;
		font-family: helveticaBold, sans-serif;
		text-align: right;
		color: rgba(50,77,161,0.5);
		padding-right: 15px;

	}

}

.nav_btn_open {
	display: block;
	background: transparent;
	border: none;
}

.nav_btn_close {
	display: none;
	background: transparent;
	border: none;

}

.menu_show {
	.nav_btn_open {
		display: none;
	}

	.nav_btn_close {
		display: block;
	}
}

.nav_active_l {
	-webkit-animation: slide-left 0.5s ease-in-out both;
	animation: slide-left 0.5s ease-in-out both;
}

.nav_active_r {
	-webkit-animation: slide-right 0.5s ease-in-out both;
	animation: slide-right 0.5s ease-in-out both;
}

@-webkit-keyframes slide-right {
	0% {
		-webkit-transform: translateX(0);
		transform: translateX(0);
		opacity: 1;
	}
	100% {
		-webkit-transform: translateX(180px);
		transform: translateX(180px);
		opacity: 0;
	}
}
@keyframes slide-right {
	0% {
		-webkit-transform: translateX(0);
		transform: translateX(0);
		opacity: 1;
	}
	100% {
		-webkit-transform: translateX(180px);
		transform: translateX(180px);
		opacity: 0;
	}
}
@-webkit-keyframes slide-left {
	0% {
		-webkit-transform: translateX(0);
		transform: translateX(0);
		opacity: 0;
	}
	100% {
		-webkit-transform: translateX(180px);
		transform: translateX(180px);
		opacity: 1;
	}
}
@keyframes slide-left {
	0% {
		-webkit-transform: translateX(180px);
		transform: translateX(180px);
		opacity: 0;
	}
	100% {
		-webkit-transform: translateX(0);
		transform: translateX(0);
		opacity: 1;
	}
}
@media (max-width: 1500px) {
	.section.hero {
		padding: 100px 0 200px;
	}
	.header {
		height: 100px;
	}

	.nav_btn_open,
	.nav_btn_close {
		padding: 0 4px 0 0;
		svg {
			width: 37px;
			height: 37px;
		}
	}
}