@import "./utils";

.blogs_page {
	background: #E5E5E5;

	&.blog {
		.hero {
			background-image: url(../images/blog_hero.jpg);
		}

		&_text {
			font-family: helveticaRegular, sans-serif;
			font-size: 30px;
			line-height: 120%;
			text-align: center;
			margin-top: 10px;
		}
	}

	.hero {
		background-image: url(../images/blogs_hero.jpg);
		height: 700px;
		min-height: initial;
		overflow: hidden;

		&_info {
			text-align: center;
		}
	}

	.hero_title {
		text-align: center;
		font-family: helveticaBlack, sans-serif;
	}

	.section.banner {
		padding: 0;
		height: 494px;
	}

	.banner_wrapper {
		background-color: rgba(73, 117, 251, 0.05);
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		background-image: url('../images/banner_img.svg');
		background-position: bottom right;
		background-repeat: no-repeat;
		background-size: auto;
		padding: 100px 70px;
	}

	.banner_title {
		font-family: helveticaBlack, sans-serif;
		font-size: 48px;
		line-height: 125%;
		margin-bottom: 25px;
		color: #4975fb;
		max-width: 780px;
	}

	.banner_text {
		margin-bottom: 40px;
		color: rgba(73, 117, 251, 0.75);
		font-size: 30px;
		line-height: 120%;
	}

	.blogs.section {
		padding-bottom: 50px;
		padding-top: 110px;

		&.blogs--top {
			padding-top: 150px;
			padding-bottom: 140px;
		}
	}

	.blogs_list {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-column-gap: 80px;
		grid-row-gap: 60px
	}

	.blogs_card {
		border-radius: 45px;
		background: #FFFFFF;
		box-shadow: 0 0 30px rgba(0, 0, 0, 0.07);
		overflow: hidden;
		border: none;
	}

	.card_img-wrapper {
		width: 100%;
		border-top-left-radius: 25px;
		border-top-right-radius: 25px;
		position: relative;

		img {
			width: 100%;
			height: 310px;
			object-fit: cover;
			position: relative;
			z-index: 0;
		}

		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			background-color: rgba(73, 117, 251, 0.25);
			z-index: 1;
			transition: background-color 0.8s ease-in-out;
		}
	}

	.card:hover {
		.card_img-wrapper:after {
			background-color: transparent;
		}
	}

	.card_body {
		padding: 25px 32px 20px
	}

	.card_title {
		display: block;
		font-size: 25px;
		line-height: 25px;
		color: #4975FB;
		font-family: helveticaBold, sans-serif;
		margin-bottom: 15px;
	}

	.card_text {
		font-size: 20px;
		line-height: 1.3;
		color: rgba(50, 77, 161, 0.5);
		font-family: helveticaMedium, sans-serif;
		margin-bottom: 22px;
		height: 78px;
		overflow: hidden;
	}

	.section.pagination {
		text-align: center;
		padding: 0 0 150px 0;

		.pagination_list {
			width: 222px;
			margin: auto;
		}

		.pagination_arrow_left,
		.pagination_arrow_right {
			background: transparent;
			border: none;
			padding: 0;
			width: 22px;
			height: 22px;
			margin: 0 5px;
			line-height: 1;
			transform: translateY(-2px);

			svg {
				width: 18px;
				height: 18px;

			}

			path {
				transition: fill 0.3s linear;
				stroke: rgba(73, 117, 851, 0.75);
			}

			&:hover {
				path {
					stroke: rgba(73, 117, 851, 0.75);
				}
			}

			&:focus {
				outline: none;
			}
		}
	}

	.pagination_item {
		line-height: 22px;
		display: inline-block;
		width: 10px;
		height: 10px;
		border-radius: 50%;
		border: none;
		margin: 11px 4px ;
		font-size: 0;
		padding: 0;
		color: rgba(73, 117, 851, 0.5);
		background: rgba(73, 117, 851, 0.5);
		transform: scale(1);
		transition: transaorm 0.3s ease-in-out,
								background-color 0.3s ease-in-out;

		&:hover {
			background: rgba(73, 117, 851, 0.75);
			transform: scale(1.1);
		}

		&.active {
			background: #4975FB;
		}
	}

	.search {
		margin-bottom: 26px;

		.title {

		}

		.form_control {
			width: 525px;
			background-image: url('../images/search.svg');
			background-repeat: no-repeat;
			background-position: right 20px center;
			background-size: auto;
			box-shadow: 0 0 30px rgba(0, 0, 0, 0.07);
			border-radius: 25px;
			height: 70px;
		}
	}
}

@media (max-width: 1500px) {
	.blogs_page {
		.blogs.section {
			padding-bottom: 29px;
			padding-top: 110px;

			.container.no_padding {
				padding: 0 25px !important;
			}
		}

		.blogs.section.blogs--top {
			padding-top: 110px;
			padding-bottom: 100px;
		}

		.pagination_item {
			width: 8px;
			height: 8px;
			margin: 2px;
			font-size: 23px;
			cursor: pointer;
			background: rgba(73, 117, 851, 0.5);
			border: none;
			padding: 0;

			&:focus {
				outline: none;
			}
		}

		.arrow {
			margin-bottom: 0;
			margin-right: 9px;
			cursor: pointer;
			background: initial;
			border: none;
			padding: 0;

			svg {
				fill: rgba(0, 117, 251, 0.25);
				transition: fill 0.3s ease-in-out;
			}

			&:hover {
				svg {
					fill: #4975FB;
				}
			}

			&:focus {
				outline: none;
			}
		}

		.search {
			margin-bottom: 40px;

			.title {
				font-size: 38px;
				line-height: 120%;
			}
		}

		.search .form_control {
			width: 393px;
			height: 55px;
			font-size: 19px;
			line-height: 55px;
		}

		.section.pagination {
			padding-bottom: 100px;

			button {
				border: none;
				padding: 0;
			}
		}

		.hero {
			height: 576px;
		}

		.hero_title {
			font-size: 45px;
			line-height: 120%;
		}

		.blogs_list {
			grid-column-gap: 79px;
			grid-row-gap: 38px;
		}

		.card_img-wrapper {
			img {
				height: 235px;
			}
		}

		.card_title {
			font-size: 19px;
			line-height: 19px;
		}

		.card {
			border-radius: 33px;
		}

		.card_body {
			padding: 19px 26px 6px;
		}


		.card_text {
			font-size: 14px;
			padding-right: initial;
			line-height: 130%;
			height: 56px;
			margin-bottom: 10px;
		}

		.banner_title {
			color: #fff;
			text-align: center;
			font-size: 43px;
			line-height: 120%;
			margin-bottom: 11px;
		}

		.banner_text {
			color: #fff;
			font-size: 22px;
			line-height: 120%;
			margin-bottom: 26px;
			text-align: center;
		}

		.section.banner {
			padding: 97px 0;
			background-image: url('../images/banner_img_md.jpg');
			height: 363px;

			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				background: rgba(73, 115, 251, 0.85);
				z-index: 0;
			}
		}

		.banner_wrapper {
			padding: 0;
			justify-content: center;
			align-items: center;
			background-image: none;
			position: relative;
			z-index: 1;

			.btn {
				text-align: center;
			}
		}
	}
}

.card_action {
	line-height: 26px;
	overflow: hidden;
	text-decoration: none;
	font-size: 20px;
	position: relative;
	width: 128px;
	font-family: helveticaMedium, sans-serif;
	transition: color 0.3s linear;
	color: rgba(73, 117, 851, 0.75);

	&:hover {
		color: rgba(73, 117, 851, 1);
		.arrow_right {
			transform: translateX(10px);
			stroke: rgba(73, 117, 851, 1);;
		}
	}
	@media (max-width: 1500px) {
		font-size: 15px;
		line-height: 20px;
	}
	@media (max-width: 1024px) {
		font-size: 14px;
		line-height: 18px;
	}

	.arrow {
		display: inline-block;
		margin-left: 10px;
		transition: transform 0.3s linear, stroke 0.3s linear;

		@media (max-width: 1500px) {
			margin-left: 4px;;
		}

		svg {
			width: 16px;
			height: 26px;

			@media (max-width: 1500px) {
				width: 12px;
				height: 20px;
			}

			@media (max-width: 1024px) {
				width: 9px;
				height: 18px;
			}
		}
	}
}

@media (max-width: 1024px) {
	.blogs_page {
		.section.blogs {
			padding-top: 100px;
			padding-bottom: 80px;

			.container.no_padding {
				padding: 0 15px !important;
			}
		}

		.blogs.section.blogs--top {
			padding-top: 68px;
			padding-bottom: 47px;

			.container.no_padding {
				padding: 0 !important;
			}
		}

		.slick-arrow {
			background: transparent;
			border: none;
			position: absolute;
			bottom: -45px;
			color: rgba(73, 117, 851, 0.75);
			font-size: 0;


			&.slick-prev {
				right: calc(50% + 10px);
				padding: 5px 15px 5px 5px;

				&:after {
					content: "\27F5";
					font-size: 20px;
					line-height: 20px;
				}
			}

			&.slick-next {
				left: calc(50% + 70px);
				padding: 5px 5px 5px 15px;

				&:after {
					content: "\27F6";
					font-size: 20px;
					line-height: 20px;
				}
			}

			&:hover {
				color: rgba(73, 117, 851, 1);
			}
		}

		.hero {
			background-image: url('../images/blogs_hero_mobile.jpg');
			height: 100vh;
		}

		.blogs_list {
			display: block;
			transform: translateX(-34px);
		}

		.slick-list {
			overflow: visible;
		}

		.slick-dots {
			display: flex;
			position: absolute;
			bottom: -28px;
			left: 50%;
			margin-left: -17px;

			button {
				background: transparent;
				padding: 0;
				width: 100%;
				height: 100%;
				border: none;
			}

			li {
				width: 10px;
				height: 10px;
				border-radius: 50%;
				margin: 3px;
				font-size: 0;
				color: rgba(73, 117, 851, 0.5);
				background: rgba(73, 117, 851, 0.5);
				transform: scale(1);
				transition: transaorm 0.3s ease-in-out,
				background-color 0.3s ease-in-out;

				&:hover {
					background: rgba(73, 117, 851, 0.75);
					transform: scale(1.1);
				}

				&.slick-active {
					background: #4975FB;
				}
			}
		}

		.blogs_card {
			margin: 12px;
			width: 260px !important;

		}

		.slick-slide:focus {
			outline: none !important;
		}

		.card_img-wrapper img {
			height: 191px;
		}

		.card_body {
			padding: 13px 20px;
		}

		.card_title {
			font-size: 15px;
			line-height: 1;
		}

		.card_text {
			max-height: 48px;
			font-size: 12px;
			line-height: 1.3;
			margin-bottom: 0;
		}

		.section.banner {
			background-image: url('../images/banner_img.svg');
			background-size: 350px;
			background-position: bottom 0 right 0;
			background-repeat: no-repeat;
			max-height: initial;
			height: auto;
			display: block;
			padding: 100px 20px;

			&:after {
				content: none;
			}

			.banner_text {
				font-size: 15px;
				line-height: 120%;
				color: rgba(73, 117, 251, 0.75);
			}

			.banner_title {
				color: #4975FB;
				font-size: 35px;
				line-height: 125%;
				text-align: center;
				margin-bottom: 15px;

				.title--green {
					color: $accent;
				}
			}

			.btn {
				margin-bottom: 196px;
			}
		}

		.search {
			margin-bottom: 20px;

			.title {
				font-size: 35px;
				line-height: 120%;
			}

			.form_control {
				background-position: right 30px center;
			}
		}

		.section.pagination {
			display: none;
		}
	}
}
